const DEFAULT_DURATION = 2000;
export default {
  name: 'toast',
  data () {
    return {
      text: '',
      showing: false,
      timer: null
    };
  },
  methods: {
    show (text, duration) {
      this.text = text;
      this.showing = true;
      if (this.timer) {
        clearInterval(this.timer);
      }
      this.timer = setTimeout(() => {
        this.showing = false;
      }, duration || DEFAULT_DURATION);
    }
  }
};



